<template>
  <div>
    <ButtonDefault
      name="Предыдущий шаг"
      color="green"
      :actionForClick="getBack"
    />
    <HouseContainer>
      <HouseFlatsSchemasList/>
    </HouseContainer>
    <ButtonDefault
      name="Следующий шаг"
      color="green"
      :actionForClick="redirectToNextStep"
    />
  </div>
</template>

<script>
import ButtonDefault from './ButtonDefault'
import HouseContainer from './HouseContainer'
import HouseFlatsSchemasList from './HouseFlatsSchemasList'

export default {
  name: 'HouseFlatsSchemas',
  components: {
    ButtonDefault,
    HouseContainer,
    HouseFlatsSchemasList
  },
  methods: {
    getBack () {
      this.$router.push({
        name: 'HouseProperties',
        params: {
          houseStoreIndex: this.storeIndex
        }
      })
    },
    redirectToNextStep () {
      this.$router.push({
        name: 'HouseFloors',
        params: {
          houseStoreIndex: this.storeIndex
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



