<template>
  <div uk-grid>
    <div class="uk-width-1-1 uk-width-1-3@m">
      <div class="uk-card uk-card-default">
        <div class="uk-card-body">
          <h2 class="uk-text-left">Новый объект</h2>
          <ButtonDefault
            name="Добавить объект вручную"
            color="grey"
            :actionForClick = "redirectToCreate"
          ></ButtonDefault>
          <ButtonDefault
            name="Создадим за вас (бесплатно)"
            color="aqua"
          ></ButtonDefault>
        </div>
        <img src="/static/img/PersonalManager.jpg" alt="">
      </div>
    </div>
      <BuildingsItem
        v-for="building of buildings"
        :key="building.id"
        :storeIndex="building.storeIndex"
        :id="building.id"
        :title="building.title"
        :imageSource="building.imageSource"
        :description="building.description"
        :apartmentBalance="building.apartmentBalance"
        :address="building.address"
        :apartmentFree="building.apartmentFree"
        @viewDetail="viewDetail"
      />
  </div>
</template>

<script>
import ButtonDefault from './ButtonDefault'
import BuildingsItem from './BuildingsItem'

export default {
  name: 'Buildings',
  data () {
    return {
      buildings: []
    }
  },
  components: {
    BuildingsItem,
    ButtonDefault
  },
  methods: {
    viewDetail (title) {
      console.log(title)
    },
    redirectToCreate () {
      this.$router.push({ name: 'BuildingProperties' })
    }
  },
  created () {
    this.$store.dispatch('destroyItemFromStore', 'currentBuildingStoreIndex')
    let companyHashId = this.$store.state.companyHashId
    this.$store.dispatch('retrieveItem', {
      url: '/company/' + companyHashId + '/buildings',
      storageName: 'buildings'
    })
      .then(response => {
        this.$store.dispatch('setItemToStore', {
          storageName: 'buildings',
          fields: response.data
        })
        let buildings = JSON.parse(response.data)
        buildings.map((building, index) => {
          let properties = building.fields
          this.buildings.push({
            'storeIndex': index,
            'id': properties.hash_id,
            'title': properties.name,
            'imageSource': properties.images,
            'description': properties.city,
            'apartmentBalance': 203,
            'address': properties.city,
            'apartmentFree': [
              { type: '2', description: 'Студия (27м)', count: 129 },
              { type: '2S', description: 'Студия (27м)', count: 129 },
              { type: 'S', description: 'Студия (27м)', count: 129 },
              { type: 'S', description: 'Студия (27м)', count: 129 }
            ]
          })
        })
      })
      .catch(error => {
        console.info(error)
      })
  }
}
</script>

<style scoped>

</style>



